import React, { useRef } from 'react';
import { LazyImage } from '../../../utils/LazyImage';
import { SideScroll } from '../../PlanPurchase/util';


function TestimonialDetail(props) {

    const ref = useRef(null);

    const HandleBack=()=>{
        SideScroll(ref.current,'left', 1, 100, 3)
    }

    const HandleNext=()=>{
        SideScroll(ref.current,'right', 1, 100, 3)
    }

    return (
        <div className="testim-maindiv">     
                  <div className="testimonails-txt">{props.data.twitterHighlight.title}</div>
            {/* <div className={`${props.data.twitterHighlight.items.length >= 2 ? "flxx33" : "flex34"} testimanialDetails`} ref={ref}> */}
            <div className="flex34">
                {props.data.twitterHighlight.items.map(e=>(
                    <div className="scrbr-cls">
                        <div className="prfl-divs">
                            <img className="profile-img" src={e.img} />
                        </div>
                    </div>
                ))}
            </div>
        </div>
    );
}

export default TestimonialDetail;