import React from 'react';

function Banner(props) {
    return (
        <>
           <div className="web-banner">
               <img src={props.data.onsurityWall.items[0].img} height="100%" width="100%" />
           </div> 

           <div className="mob-banner">
               <img src="https://res.cloudinary.com/onsurity/image/upload/v1628230356/New-home-screen/Group_8004_tmy3wr.svg" width="100%"  />
           </div>
        </>
    );
}

export default Banner;