import Axios from 'axios';
import React, { Suspense, useEffect, useState } from 'react'
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { bindActionCreators } from 'redux';
import FooterComponent from '../../components/Common/Footer/footer';
import Header from '../../components/Home/HeaderSection/Header';
import { notify } from '../../Input-controls/Toast/Toast';
import { actions as StateActions } from '../../redux/ducks/state_change';
import { urls } from '../../routes/routes';
import { navigation } from '../../utils/Env';
import Loader from '../../utils/Loader/Loader';
import { urlPrefixEmpSure, urlPrefixGlobal } from '../../utils/MicroServiceURL';
import Banner from './Components/Banner';
import CustomerReview from './Components/CustomerReview';
import FounderReview from './Components/FounderReview';
import GetInTouch from './Components/GetInTouch';
import { Testimonial_Data } from './Components/TestimonialData';
import TestimonialDetail from './Components/TestimonialDetail';
import Videosec from './Components/Videosec';
import './Testimonial.scss'
const Slides = React.lazy(() => import('../../components/Common/Sliders/Slide5/Slide'));

const slide1 = [
    {
        "head": "",
        "sub": "",
        "currentCls": "",
        "imgPath": "https://res.cloudinary.com/onsurity/image/upload/fl_lossy,f_auto,q_auto/v1594275941/New-home-screen/HomeSlide_1/slide1_1_djvzzu.svg"
    }
]

function Testimonial(props) {

    const[shoutoutWallIpp, setshoutoutWallIpp] = useState()
    const[socialProofIpp,setsocialProofIpp] = useState()
    const[TestimonialData, setTestimonialData] = useState()

    useEffect(() => {
        Axios.post(urlPrefixGlobal('/web-contents'),{
            "page":"testimonial",
            "shoutoutWall": {
                "itemsPerPage": shoutoutWallIpp
            },
            "socialProof": {
                "itemsPerPage": socialProofIpp
            }
        })
            .then(res=>{
                if(res.data.success=== 1){ 
                    setTestimonialData(res.data.data); 
                    setcustomerreviewLoading(false)
                    setfounderreviewloading(false)  
                    console.log(res.data, 'uytwd67')         
                }
            })
            .catch(err=>{
                notify.danger("Something went wrong")
                setcustomerreviewLoading(false)
                setfounderreviewloading(false)
                // if(err && err.response){
                //     console.log(err.response)
                //     msg.error(err.response.data.msg)
                // }
                })
      },[shoutoutWallIpp, socialProofIpp])

      const[customerreviewLoading, setcustomerreviewLoading]=useState(false)
      const[founderreviewloading, setfounderreviewloading] = useState(false)
      
      const ViewMoreItemsCustomerReview=()=>{
        setcustomerreviewLoading(true)
        setshoutoutWallIpp((prev)=>prev+prev)
      }

      const ViewMoreItemFounderReview=()=>{
          setfounderreviewloading(true)
          setsocialProofIpp((prev)=>prev+prev)
      }

    const[callUrl, setcallUrl] = useState(true)

    return (
        TestimonialData?

        <div>
             <Header />
        
            <Banner data={TestimonialData} />
             <TestimonialDetail data={TestimonialData} />
           <GetInTouch 
                title={TestimonialData.healthCareExperience.title} 
                btnText={TestimonialData.healthCareExperience.items[0].btn}
                callUrl={callUrl}
                link={urls.VERIFY_PHONE}
                 />
            <CustomerReview loading={customerreviewLoading} data={TestimonialData} TestimonialEvent={ViewMoreItemsCustomerReview}/>
            <Videosec data={TestimonialData}/>
            <GetInTouch 
                title={TestimonialData.onsurityForYourTeam.title} 
                btnText={TestimonialData.onsurityForYourTeam.items[0].btn}
                link={urls.CONTACT_US_NEW}
                 />
            <FounderReview loading={founderreviewloading} data={TestimonialData} TestimonialEvents={ViewMoreItemFounderReview}  />
            <div style={{marginTop: '80px'}}>
               <GetInTouch 
                      title={TestimonialData.leadTheWayWithHealthCare.title} 
                      btnText={TestimonialData.leadTheWayWithHealthCare.items[0].btn}
                    link={urls.VERIFY_PHONE}
                     />
            </div>
           
            <div className="footer-compo">
                <FooterComponent />
            </div>

        </div>
        :
        <Loader/>
    )
}

const mapDispatchToProps = (dispatch) => ({
    ...bindActionCreators({
        ...StateActions,
        //ADD IF ITS FROM DIFFRENT ACTION FILE
    }, dispatch)
  });

export default withRouter(connect(null, mapDispatchToProps)(Testimonial));
