import React from 'react';

function Videosec(props) {
    return (
        <div className="onsurity-youtubevido">
            <div className="video-txt">This is why we do <br/> what we do</div>
            <div align="center" className="iframe-div" style={{marginTop: '40px'}}>
                <iframe 
                    className="iframe-cls"
                     src={props.data.mediaSection.items[0].mediavideo} >
                     {/* "https://www.youtube.com/embed/TdKAEdPR_3Q?autoplay=0&mute=1" */}
                </iframe>        
            </div>
        </div>
    );
}

export default Videosec;