import React, { useState } from 'react';
import Button from '../../../Input-controls/Button/Button';
import { LazyImage } from '../../../utils/LazyImage';

function CustomerReview(props) {


  const loadContent = () =>{
       props.TestimonialEvent()
  }

  const { info, totalCount } = props.data.shoutoutWall;

    return (
        <div className="maindiv-review">
            <div className="testimonails-txt" style={{marginBottom: '30px'}}>{props.data.shoutoutWall.title}</div>
    
          <div>
        
             <div className="review-clss">
                {props.data.shoutoutWall.info.map((e, index)=>(
                        <div className={`${index%2===0 ?"tetmoni-flexxx":"tetmoni-flex"} `}>
                        <div className={`${index%2===0 ?"block-gry":"block"} `}>
                      <div className="quote-image">
                                <LazyImage src="https://res.cloudinary.com/onsurity/image/upload/v1628150058/New-home-screen/oflrlryfebtdrhbevkyo.svg" className="imghgt" />
                            </div>
                            <div className="shoutout-content">{e.description}</div>
                            
                          <div className={`${index%2===0 ?"pin-gry":"pin"} `}></div>
                        </div>
                        <div className="d-flex empdetails">
                              <div className="name-cmpny"><span style={{fontFamily: 'Gilroy'}}>{e.name}</span>
                                    <div className="company-roleid">{e.role}</div>
                              </div>
                            <div className="logo-rgt">
                                <LazyImage src={e.companylogo}  className="cmplogo-hgt"  />
                            </div>
                        </div>
                      </div>       
                  ))}
                </div>
          </div>
          {/* {props.loading===true ? 
              <div className="testimonialloader"></div>
                :
                  <>
                  {info.length!==totalCount  &&
                    <div className="ctablock">
                      <Button 
                         onClick={loadContent}
                         className="viewmore-cta"
                         value="View More" />
                    </div>}
                    </>
                } */}
               
        </div>
    );
}

export default CustomerReview;