import React, { useEffect, useState } from 'react';
import Button from '../../../Input-controls/Button/Button';
import { LazyImage } from '../../../utils/LazyImage';
import { data } from '../../Details/Details_data';

function FounderReview(props) {

    const loadContent = () =>{
        props.TestimonialEvents()
   }

   const[data, setData]=useState({
       rowOne:[],
       rowTwo:[]
   })

   const { items, totalCount }=props.data.socialProof;

   useEffect(()=>{
       let dta={
        rowOne:[],
        rowTwo:[]
       }
       let current="row1"
        items.map(e=>{
            if(current=="row1"){
                dta.rowOne.push(e)
                current="row2"
            }else{
                dta.rowTwo.push(e)
                current="row1"
            }
        })
        setData(dta)
   },[items])

    return (
            <div>
                <div className="testimonails-txtss">{props.data.socialProof.title}</div>

                <div className="review-imgflx">
                      
                    <div className="rev-image11">
                        {data.rowOne.map(e=>(
                            <div>
                                <LazyImage style={{marginTop:'15px'}} src={e.img} width="100%" />
                            </div>
                        ))}
                    </div>
                    <div className="rev-image">
                        {data.rowTwo.map(e=>(
                            <div>
                                <LazyImage style={{marginTop:'15px'}} src={e.img} width="100%" />
                            </div>
                        ))}
                    </div>
                </div>
                {/* {props.loading===true ? 
                    <div className="testimonialloader"></div>
                :
                <>
                    {items.length!==totalCount  &&
                        <div className="ctablock">
                        <Button
                            onClick={loadContent}
                            className="viewmore-cta"
                            value="View More" />
                    </div>}
               </>} */}

            </div>
    );
}

export default FounderReview;